import { __decorate } from "tslib";
import { Component, Vue, Watch } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import commodityAdd from './commodity-add.vue';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        this.classId = '';
        this.className = '';
        this.commodityList = [];
        //5693c8b0-f97f-11ea-8165-3111824eebe8
        this.showAdd = false;
        this.commodityObj = {
            name: '',
            price: 0,
            unit: '',
            disabled: false,
            number: 0,
            imgUrlList: []
        };
    }
    activated() {
        let queryObj = this.$route.query;
        this.classId = queryObj.id;
        this.className = queryObj.name;
        this.showAdd = false;
    }
    deactivated() {
        this.classId = '';
        this.$set(this, 'commodityList', []);
    }
    idChange(newId) {
        if (newId) {
            this.getCommodityListByClassId();
        }
    }
    getCommodityListByClassId() {
        if (!this.classId) {
            return;
        }
        const toast = this['$createToast']({
            time: 0,
            txt: '商品获取中...'
        });
        toast.show();
        this.$store.dispatch('getCommodityListByClassId', { class: this.classId, page: 1 }).then((response) => {
            if (response.success) {
                let list = response.data.list;
                this.$set(this, 'commodityList', list);
                toast.hide();
            }
        });
    }
    addCommodity() {
        this.commodityObj = {
            name: '',
            price: 0,
            unit: '',
            disabled: false,
            number: 0,
            imgUrlList: []
        };
        this.showAdd = true;
    }
    addCancel() {
        this.showAdd = false;
    }
    saveSuccess(type, data) {
        if (type === 'add') {
            this.commodityList.push(data);
        }
        else {
            let index;
            this.commodityList.some((item, index) => {
                if (data.id === item.id) {
                    index = index;
                    return true;
                }
            });
            this.$set(this.commodityList, index, data);
        }
        this.addCancel();
    }
    editCommodity(item) {
        this.commodityObj = item;
        this.showAdd = true;
    }
    mounted() {
        // this.$router.beforeEach((to, form, next) => {
        //   if (to.name === 'commodityManageClass') {
        //     if (!this.showAdd) {
        //       next()
        //     } else {
        //       this.showAdd = false
        //     }
        //   }
        // })
    }
};
__decorate([
    Watch('classId')
], AppList.prototype, "idChange", null);
AppList = __decorate([
    Component({
        components: {
            page,
            commodityAdd
        }
    })
], AppList);
export default AppList;
