            price: 0,
            unit: '',
            disabled: false,
            number: 0,
            imgUrlList: []
        };
        this.fields = [
            {
                type: 'input',
                modelKey: 'name',
                label: '商品名称',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'input',
                modelKey: 'price',
                label: '价格',
                props: {
                    placeholder: '请输入价格'
                },
                rules: {
                    required: true,
                    type: 'number'
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'select',
                modelKey: 'unit',
                label: '单位',
                props: {
                    options: ['个', '斤', '件', '箱', '平米', '条']
                },
                rules: {
                    required: true
                }
            },
            {
                type: 'switch',
                modelKey: 'disabled',
